import React from 'react';
import pf from '../assets/portfolio.png';

function close_proj(){
    document.getElementById('proj_cont_all').click();
}

const projDesc = [
    {
        wrapperStyle:{display:'none'},
        id:"Portfolio",
        header:"Portfolio",
        iframeSrc: pf,
        name: "Personal Portfolio",
        credits: "Hobby Project",
        desc:[
            {val:"I wanted to build my own portfolio to showcase my skills, experience, and show my work rather than just typing it out in resume."},
            {val:"The site is build using plain HTML, CSS, JS."},
            {val:"Framework used are bootstrap and jquery."},
            {val:"Future work is to integrate ReactJS into this."},
            {val:"Source Code - ",ref:"https://github.com/adityasingh3007/adityasingh3007.github.io",tag:"Github"}
        ]
    },
    {
        wrapperStyle:{display:'none'},
        id:"FishBOT",
        header:"Bio-Inspired Sub-Carangiform Fish BOT",
        iframeSrc: "",
        name: "BIO-INPIRED SUB-CARANGIFORM FISH BOT",
        credits: "Summer Internship @eYSIP-2019",
        desc:[
            {val:"A BOT which performs basic actions done by a fish having Body Caudal Fin Propulsion(BCF) locomotion of the type sub-carangiform."},
            {val:"Head of the body is 3D printed. Rest half is made using acrlyic sheets."},
            {val:"Arduino Nano is used for programming."},
            {val:"Uses Wi-Fi to communicated with the remote. NRF24L01 module is used."},
            {val:"Undulatory motion."},
            {val:"Read more - ",ref:"https://github.com/adityasingh3007/Bio-Inspired-Sub-Carangiform-FishBOT",tag:"Github"}
        ]
    },
    {
        wrapperStyle:{display:'none'},
        id:"AntBOT",
        header:"Ant BOT",
        iframeSrc: "https://www.youtube.com/embed/XipXBkDzuBQ",
        name: "ANT BOT",
        credits: "Theme @eYRC-2018",
        desc:[
            {val:"A BOT which performs basic actions done by ants such as collecting leaves, honey dews etc and removing trash from their hills."},
            {val:"Pick and place mechanism is made from scratch using measuring scales and hobby servo motors."},
            {val:"Arduino Nano and Raspberry Pi v3 is used."},
            {val:"Uses Pi-Cam to detect ArUco markers and blocks."},
            {val:"Read more - ",ref:"https://github.com/adityasingh3007/AntBOT",tag:"Github"}
        ]
    },
    {
        wrapperStyle:{display:'none'},
        id:"PortableBraille",
        header:"Portable Braille",
        iframeSrc: "https://www.youtube.com/embed/wZ1dMdXMtPE",
        name: "PORTABLE BRAILLE",
        credits: "Sangam @Pragyan-2019 ",
        desc:[
            {val:"A reading assistive device for visually impaired people."},
            {val:"Google's 'pytesseract' library is used for OCR."},
            {val:"Various feature such as- Speed control, Grade selection."},
            {val:"Supports both Grade 1 and Grade 2 braille."},
            {val:"Braille terminal is made using DOT MATRIX printer head. "},
            {val:"Read more - ",ref:"https://github.com/adityasingh3007/Portable_Braille",tag:"Github"}
        ]
    },
    {
        wrapperStyle:{display:'none'},
        id:"GestureControlled",
        header:"Gesture Controlled Remote Controller",
        iframeSrc: "https://www.youtube.com/embed/C6ktntQEmCk",
        name: "GESTURE CONTROLLED REMOTE CONTROLLER",
        credits: "-CON'18 Workshop @Spider R&D Club, NIT Trichy",
        desc:[
            {val:"A wireless gesture recognition setup to play various PC games."},
            {val:"ATMEGA328p is used. In transmitter side Arduino Nano, in reciever side Arduino Uno."},
            {val:"Uses NEC Protocol for wireless communication."},
            {val:"Racing games can be easily played."},
            {val:"Other games can also be played with little bit of changing the code."},
            {val:"Read more - ",ref:"https://github.com/adityasingh3007/Gesture-Controlled-Wireless-Game-Controller",tag:"Github"}
        ]
    }
]

class ProjBanner extends React.Component{
    render(){
        return(
            <>
                <div className="close_cont" onClick={close_proj}>Close</div>
                {projDesc.map((proj, key)=>{
                    return(
                        <div key={key} className="project_desc_wrapper" style={proj.wrapperStyle} id={proj.id}>
                            <div className="proj_cont">
                                <div className="proj_name">{proj.header}</div>
                                <div className="proj_main_cont"><br/><br/>
                                    <div>
                                        {
                                            proj.id === "Portfolio" ? 
                                            <img src={proj.iframeSrc} alt="Portfolio" style={{width:100+"%"}}/>
                                            :
                                            <iframe title={proj.id} src={proj.iframeSrc} frameBorder="0" allow="accelerometer; autoplay; encrypted-media; gyroscope; picture-in-picture" allowFullScreen style={{width:100+'%',height:400+'px',border:'none'}}></iframe>
                                        }
                                    </div><br/>
                                    <div className="pdc">
                                        <div className="proj_name_small">
                                            {proj.name}
                                            <div className="_credits"><h5>{proj.credits}</h5></div>
                                        </div>
                                        <div className="src_link">
                                            <ul>
                                                {(proj.desc).map((list,key)=>{
                                                    return(
                                                            <li key={key}>
                                                                {list.ref?
                                                                    <>
                                                                        {list.val}<a href={list.ref} title={list.tag}>{list.tag}</a>
                                                                    </>
                                                                    :
                                                                    list.val
                                                                }
                                                            </li>
                                                    );
                                                })}
                                            </ul>
                                            <br/><br/><br/>
                                        </div>
                                    </div>
                                </div>
                            </div>
                        </div>
                    );
                })}
            </>
        );
    }
}

export default ProjBanner;