import React from 'react';
import cpp from '../assets/c++.png';
import python from "../assets/python.png";
import go from "../assets/go.png";
import html from "../assets/html.png";
import css from "../assets/css.png";
import js from "../assets/javascript.png";
import php from "../assets/php7.png";
import bootstrap from "../assets/bootstrap.png";
import jquery from "../assets/jquery.png";
import mysql from "../assets/mysql.png";
import embedded from "../assets/embedded.png";
import git from "../assets/git.png";


const skillSet = [
    {
        name: "C++",
        importName: cpp,
        style:{width:100+'px'}
    },
    {
        name: "Python",
        importName: python,
        style:{width:110+'px'}
    },
    {
        name: "Go",
        importName: go,
        style:{width:180+'px',marginTop:20+'px'}
    },
    {
        name: "HTML",
        importName: html,
        style:{width:110+'px'}
    },
    {
        name: "CSS",
        importName: css,
        style:{width:80+'px'}
    },
    {
        name: "JavaScript",
        importName: js,
        style:{width:185+'px'}
    },
    {
        name: "PHP",
        importName: php,
        style:{width:85+'px'}
    },
    {
        name: "Bootstrap",
        importName: bootstrap,
        style:{width:120+'px'}
    },
    {
        name: "jQuery",
        importName: jquery,
        style:{width:200+'px'}
    },
    {
        name: "MySQL",
        importName: mysql,
        style:{width:150+'px'}
    },
    {
        name: "Embedded C",
        importName: embedded,
        style:{width:100+'px'}
    },
    {
        name: "Git",
        importName: git,
        style:{width:150+'px'}
    }
]

class Skill extends React.Component{
    render(){
        return (
            <div className="sec_cover">
              <div className="sec_head">Skills</div>
              <div className="headdesc">Skills I have</div>
              <div className="sk_img_cont">
                  <div className="row_">
                      {skillSet.map((data, key)=>{
                          return(
                            <div key={key} className="column">
                                {
                                    <img alt="Resource not available" src={data.importName} title={data.name} style={data.style} />
                                }
                            </div>
                          );
                      })}
                  </div>
              </div>
              <div className="skills_add">..... and many more. Check it out in the <b>resume</b>.</div>
          </div>
        );
    }
}

export default Skill;