import React from 'react';
import ReactDOM from 'react-dom';
import Lander from '../Components/Landing.js';
import Project from '../Components/Project.js';
import ProjBanner from '../Components/Banner.js';

const projScript = () => import("../util/projects.js");
const projCSS = () => import('../css/projects.css');
let cssLoaded = false;
const proj_ban = document.getElementById('proj_cont_all');

class ProjectPage extends React.Component{
    render(){
        if (cssLoaded === false) {
            cssLoaded = true;
            projCSS();
        }
        return (
            <>
                <section className="sec_1 sec_com" id="home"><Lander type="project"/></section>
                <section className="sec_2 sec_com" id="projects"><Project brand="project"/></section>
            </>
        );
    }
    componentDidMount(){
        projScript();
        ReactDOM.render(<ProjBanner/>,proj_ban);
        document.getElementById('lander_scroller').href="#projects";
    }
}

export default ProjectPage;