import React from 'react';
import { Link } from 'react-router-dom';

function show_sidebar() {
    document.getElementById("main").style.left="-290px";
    document.getElementById("header").style.marginLeft="-290px";
    document.getElementById("sdb_cvr").style.display="block";
    document.getElementById("sidebar").style.width="290px";
    document.getElementById("menu_").style.display="none";
}

function hide_side_bar() {
  document.getElementById("main").style.left="0px";
  document.getElementById("header").style.marginLeft="0px";
  document.getElementById("sdb_cvr").style.display="none";
  document.getElementById("sidebar").style.width="0px";
  document.getElementById("menu_").style.display="block";
}

class NavListProj extends React.Component{
    render(){
        let {brand}=this.props;
        let state = brand.brand;
        return(
            <li>
                {state?
                    <Link to="/#projects" className="h_menu_list">Projects</Link>
                    :
                    <Link to="/project/#projects" className="h_menu_list">Projects</Link>
                }
            </li>
        );
    }
}

class Header extends React.Component{
    render(){
        let state = this.props;
        return (
              <div className="header_cont">
                  <div className="header_part header_logo" id="main_logo" style={{opacity:0,transition: 'all '+0.5+'s ease'}}>
                          <Link to="/" style={{color:'#fff',textDecoration:'none'}}>Aditya Kumar Singh</Link>
                  </div>
                  <div className="header_part header_menu_cont">
                    <nav className="navbar nav-bar-o">
                        <ul className="nav nav_bar">
                            <li><Link to="/#home" className="h_menu_list list_special">Home</Link></li>
                            <li><Link to="/#about" className="h_menu_list">About</Link></li>
                            <li><Link to="/#skills" className="h_menu_list">Skills</Link></li>
                            <NavListProj brand={state}/>
                            <li><Link to="/#resume" className="h_menu_list">Resume</Link></li>
                            <li><Link to="/#contact" className="h_menu_list">Contact</Link></li>
                        </ul>
                    </nav>
                      <div className="h_menu_icon" id="menu_" onClick={show_sidebar}>
                          <div className="h_menu_bar"></div>
                          <div className="h_menu_bar"></div>
                          <div className="h_menu_bar"></div>
                      </div>
                  </div>
                  <div className="sidebar_cover" id="sdb_cvr" onClick={hide_side_bar}></div>
                  <div className="sidebar" id="sidebar">
                          <div className="close_sidebar" onClick={hide_side_bar}>
                              <i className="fas fa-times"></i>
                          </div>
                          <div className="sidebar_cont">
                                  <div className="header_logo">Aditya Kumar Singh</div>
                              <nav className="navbar nav-bar-o">
                                    <ul className="nav nav_bar_side">
                                        <li><Link to="/#home" className="h_menu_list ">Home</Link></li>
                                        <li><Link to="/#about" className="h_menu_list">About</Link></li>
                                        <li><Link to="/#skills" className="h_menu_list">Skills</Link></li>
                                        <NavListProj brand={state}/>
                                        <li><Link to="/#resume" className="h_menu_list">Resume</Link></li>
                                        <li><Link to="/#contact" className="h_menu_list">Contact</Link></li>
                                    </ul>
                              </nav>
                          </div>
                  </div>
              </div>
        );
    }
}

export default Header