import React from 'react';
import pf from '../assets/portfolio.png';
import FishBOT from "../assets/FishBOT.jpg";
import AntBOT from "../assets/AntBOT.jpg";
import PB from "../assets/Portable_Braille.jpg";
import gc from "../assets/GestureControlledRemoteController.png";
import multi from "../assets/multi_proj.jpg";
import {Link} from 'react-router-dom';

const projectList = [
    {
        src: pf,
        name: "Portfolio",
        link: "#Portfolio" 
    },
    {
        src: FishBOT,
        name: "Bio-Inspired Sub-Carangiform FishBOT",
        link: "#FishBOT" 
    },
    {
        src: AntBOT,
        name: "AntBOT",
        link: "#AntBOT" 
    },
    {
        src: PB,
        name: "Portable Braille",
        link: "#PortableBraille" 
    },
    {
        src: gc,
        name: "Gesture Controlled Remote Controller",
        link: "#GestureControlled" 
    }
]
class ProjectLander extends React.Component{
    render(){
        return(
            <div className="pr_cover">
              <div className="sec_cover">
                  <div className="sec_head">Projects</div>
                  <br/><br/><br/>
                  <div className="project_title_cont" style={{visibility:'hidden'}}>
                      <div className="row_">
                            {projectList.map((data, key)=>{
                                return(
                                    <div key={key}  className="col">
                                        <div className="project_wrapper">
                                            <div className="container_">
                                                <img src={data.src} alt="Resource not available" className="image"/>
                                                <div className="overlay">
                                                    <div className="text">
                                                        <div className="text-wrapper">
                                                            {data.name}
                                                        </div>
                                                        <Link to={'/project/'+data.link}><div className="proj_but">View More</div></Link>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                );
                            })}
                          <div className="col">
                              <div className="project_wrapper">
                                  <div className="container_">
                                      <img src={multi} alt="Resource not available" className="image"/>
                                      <div className="overlay">
                                          <div className="text">
                                              More Projects<br/>
                                              <Link to="/project"><div className="proj_but">View All</div></Link>
                                          </div>
                                      </div>
                                  </div>
                              </div>
                          </div>
                      </div>
                  </div>
              </div>
          </div>
        );
    }
}

class ProjectMain extends React.Component{
    render(){
        return(
            <div>
                <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100" preserveAspectRatio="none">
                    <polygon points="0,100 90,20 100,30 100,100" fill="#E9EBEE" opacity="0.7"/>
                </svg>
                <div className="sec_cover">
                    <div className="sec_head">All Projects</div>
                    <br/><br/><br/>
                    <div className="project_title_cont">
                        <div className="row_">
                            {projectList.map((data, key)=>{
                                return(
                                    <div key={key}  className="col">
                                        <div className="project_wrapper">
                                            <div className="container_">
                                                <img src={data.src} alt="Resource not available" className="image"/>
                                                <div className="overlay">
                                                    <div className="text">
                                                        <div className="text-wrapper">
                                                            {data.name}
                                                        </div>
                                                        <Link to={'/project/'+data.link}><div className="proj_but">View More</div></Link>
                                                    </div>
                                                </div>
                                            </div>
                                        </div>
                                    </div>
                                );
                            })}
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

class Project extends React.Component{
    render(){
        let {brand}=this.props;
        if(brand==="home"){
            return <ProjectLander/>
        }
        else{
            return <ProjectMain/>
        }
    }
}
export default Project