import React from 'react';
import Lander from '../Components/Landing.js';
import About from '../Components/About.js';
import Skill from '../Components/Skills.js';
import Project from '../Components/Project.js';
import Resume from '../Components/Resume.js';
import Contact from '../Components/Contact.js';

let mainScript = () => import("../util/main.js");

class Home extends React.Component{
    render(){
        return (
            <>
                <section className="sec_1 sec_com" id="home"><Lander brand="home" /></section>
                <section className="sec_2 sec_com" id="about"><About/></section>
                <section className="sec_3 sec_com" id="skills"><Skill/></section>
                <section className="sec_4 sec_com" id="projects"><Project brand="home"/></section>
                <section className="sec_5 sec_com" id="resume"><Resume/></section>
                <section className="sec_6 sec_com" id="contact"><Contact/></section>
            </>
        );
    }
    componentDidMount(){
        mainScript();
    }
}

export default Home