import React from 'react';
import res from '../assets/resume.jpg';
import pdf from '../assets/Aditya_Kumar_Singh_Resume.pdf';

class Resume extends React.Component{
    render(){
        return (
            <div>
                <div className="sec_cover">
                  <div className="sec_head" id="resume_head">Resume</div>
                </div>
                <div className="rs_cont">
                    <div className="row_">
                        <div className="column_1">
                            <img src={res} alt="Profile Pic" style={{width:400+'px'}} id="resume_image"/>
                        </div>
                        <div className="column_1" >
                            <br/>
                            <br/><br/><center>
                                <div className="download_res_cont" id="resume_content">
                                        Choose a job you love, and you will never have to work a day in your life.
                                        <br/>
                                        <span style={{float:'right'}}>-Confucius</span>
                                    <br/><br/><br/>
                                    <a href={pdf} download><button className="btn_">Download</button></a>
                                </div></center>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
}

export default Resume;