import React from 'react'
import {Link} from 'react-router-dom'
class Error extends React.Component{
    render(){
        return(
            <>
                <div className="home_cont" id="home-cont">
                    <div className="intro_card_cont" id="tiltcontainer" style={{textAlign:'center'}}>
                        <div className="card_name" style={{textAlign:'center'}}>404</div>
                        <div className="card_desc" style={{textAlign:'center'}}>
                            <span className="txt-rotate">That's an error.</span>
                        </div>
                        <Link to="/"><button className="errorbut draw">Go Back</button></Link>
                    </div>
                    <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100" preserveAspectRatio="none">
                        <polygon className="svg--sm" fill="white" points="0,0 30,100 65,21 90,100 100,75 100,100 0,100"/>
                        <polygon className="svg--lg" fill="white" points="0,0 15,100 33,21 45,100 50,75 55,100 72,20 85,100 95,50 100,80 100,100 0,100" />
                    </svg>
                </div> 
            </>
        );
    }
    componentDidMount(){
        document.getElementById("backto").style.display="none";
        document.getElementById("lander_scroller").style.display="none";
    }

}

export default Error;