import React from 'react'
import Header from './Header.js'

class Lander extends React.Component{
    render(){
        let {brand}=this.props;
        let state = false;
        if(brand==="home"){
            state = true;
        }
        return (
        <div>
            <header className="header" id="header">
                <Header brand={state}/>
            </header>
            {state? <div className="home_cont" id="home-cont">
                            <div id="imgcoder"></div>
                            <div className="intro_card_cont" id="tiltcontainer">
                                <div className="card_name">Aditya <br/>Kumar Singh</div>
                                <div className="card_desc">
                                    <span className="txt-rotate" data-period="2000" data-rotate='[ "Engineer", " Programmer", "Web Developer"]'></span>
                                </div>
                            </div>
                            <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100" preserveAspectRatio="none">
                                <polygon className="svg--sm" fill="white" points="0,0 30,100 65,21 90,100 100,75 100,100 0,100"/>
                                <polygon className="svg--lg" fill="white" points="0,0 15,100 33,21 45,100 50,75 55,100 72,20 85,100 95,50 100,80 100,100 0,100" />
                            </svg>
                        </div> 
                    :
                    <div className="home_cont">
                        <div className="intro_card_con">
                            <div className="card_name">Projects</div>
                            <div className="card_desc_"> Home / Projects </div>
                        </div>
                        <svg xmlns="http://www.w3.org/2000/svg" viewBox="0 0 100 100" preserveAspectRatio="none">
                            <polygon className="svg--sm" fill="white" points="0,0 30,100 65,21 90,100 100,75 100,100 0,100"/>
                            <polygon className="svg--lg" fill="white" points="0,0 15,100 33,21 45,100 50,75 55,100 72,20 85,100 95,50 100,80 100,100 0,100" />
                        </svg>
                    </div> 
            }
        </div>
        );
    }
    componentDidMount(){
        let {brand}=this.props;
        if(brand==="home"){
            var container = document.getElementById('tiltcontainer');
            var inner = document.getElementById('imgcoder');
                
            var counter = 0;
            var updateRate = 10;
            var isTimeToUpdate = function() {
            return counter++ % updateRate === 0;
            };

            var mouse = {
                _x: 0,
                _y: 0,
                x: 0,
                y: 0,
                updatePosition: function(event) {
                var e = event || window.event;
                this.x = e.clientX - this._x;
                this.y = (e.clientY - this._y) * -1;
                },
                setOrigin: function(e) {
                this._x = e.offsetLeft + Math.floor(e.offsetWidth/2);
                this._y = e.offsetTop + Math.floor(e.offsetHeight/2);
                },
                show: function() { return '(' + this.x + ', ' + this.y + ')'; }
            }
            mouse.setOrigin(container);

            var update = function(event) {
                mouse.updatePosition(event);
                updateTransformStyle(
                (mouse.y / inner.offsetHeight).toFixed(2),
                (mouse.x / inner.offsetWidth).toFixed(2)
                );
            };
            
            var updateTransformStyle = function(x, y) {
                var winwidth = window.innerWidth;
                var style;
                if(winwidth<=1000){
                style = "scale(0.9) rotateX(" + x + "deg) rotateY(" + y + "deg)";
                }
                else{
                style = "rotateX(" + x + "deg) rotateY(" + y + "deg)";
                }
                inner.style.transform = style;
                inner.style.webkitTransform = style;
                inner.style.mozTransform = style;
                inner.style.msTransform = style;
                inner.style.oTransform = style;
            };

            var onMouseEnterHandler = function(event) {
                update(event);
            };
            var onMouseLeaveHandler = function() {
                inner.style = "";
            };
            var onMouseMoveHandler = function(event) {
            if (isTimeToUpdate()) {
                update(event);
            }
            };

            container.onmouseenter = onMouseEnterHandler;
            container.onmouseleave = onMouseLeaveHandler;
            container.onmousemove = onMouseMoveHandler;
        }
    }
}

export default Lander